export class OrderPaymentContentView {
    /**
     * @param templateId - id 
     * @param onNext - ,      (  )
     * @param onInputChanged -      
     */
    constructor(templateId, onNext, onInputChanged) {
        this.nextButton = null;
        this.errorElement = null;
        this.onNext = onNext;
        this.onInputChanged = onInputChanged;
        const template = document.getElementById(templateId);
        if (!template) {
            throw new Error(`OrderPaymentContentView:  `);
        }
        const clone = template.content.cloneNode(true);
        const element = clone.firstElementChild;
        if (!element) {
            throw new Error(`OrderPaymentContentView:  `);
        }
        this.contentElement = element;
    }
    render() {
        const form = this.contentElement.querySelector('form');
        if (form) {
            form.addEventListener('submit', (e) => {
                e.preventDefault();
            });
        }
        const btnOnline = this.contentElement.querySelector('button[name="card"]');
        const btnCash = this.contentElement.querySelector('button[name="cash"]');
        const addressInput = this.contentElement.querySelector('input[name="address"]');
        this.nextButton = this.contentElement.querySelector('button.order__button');
        this.errorElement = this.contentElement.querySelector('.form__errors');
        if (this.nextButton) {
            this.nextButton.disabled = true;
        }
        btnOnline.addEventListener('click', () => {
            btnOnline.classList.add('button_alt-active');
            btnCash.classList.remove('button_alt-active');
            this.onInputChanged('payment', 'online');
        });
        btnCash.addEventListener('click', () => {
            btnCash.classList.add('button_alt-active');
            btnOnline.classList.remove('button_alt-active');
            this.onInputChanged('payment', 'cash');
        });
        addressInput.addEventListener('input', () => {
            this.onInputChanged('address', addressInput.value);
        });
    }
    /**
     *     "".
     * @param callback - ,      "".
     */
    bindNextButton(callback) {
        if (this.nextButton) {
            this.nextButton.addEventListener('click', callback);
        }
    }
    /**
     *       .
     * @param errorMessage -  .
     */
    setError(errorMessage) {
        if (this.errorElement) {
            this.errorElement.textContent = errorMessage;
        }
    }
    /**
     *    "".
     * @param enabled - true,     .
     */
    setButtonState(enabled) {
        if (this.nextButton) {
            this.nextButton.disabled = !enabled;
        }
    }
    /**
     *   DOM- .
     */
    getContent() {
        return this.contentElement;
    }
}
