/**
 *   .
 *      .
 *       onProductClick   .
 */
export class StorePageView {
    /**
     * @param containerSelector -  ,     .
     * @param productCardViewConstructor - ,   ProductCardView    .
     * @param onProductClick - ,      ;   .
     */
    constructor(containerSelector, productCardViewConstructor, onProductClick) {
        const container = document.querySelector(containerSelector);
        if (!container) {
            throw new Error(`StorePageView:  `);
        }
        this.container = container;
        this.productCardViewConstructor = productCardViewConstructor;
        this.onProductClick = onProductClick;
    }
    /**
     *   :     ,       onProductClick.
     * @param productList -   .
     */
    render(productList) {
        this.container.innerHTML = '';
        productList.forEach((product) => {
            const cardView = this.productCardViewConstructor(() => {
                this.onProductClick(product);
            });
            cardView.render(product);
            this.container.appendChild(cardView.getElement());
        });
    }
}
