/**
 *       .
 */
export class OrderModel {
    constructor(apiClient) {
        this.payment = null;
        this.address = '';
        this.email = '';
        this.phone = '';
        this.items = [];
        this.total = 0;
        this.apiClient = apiClient;
    }
    // 
    setPayment(payment) {
        this.payment = payment;
    }
    setAddress(address) {
        this.address = address;
    }
    setContacts(email, phone) {
        this.email = email;
        this.phone = phone;
    }
    setItems(itemIds) {
        this.items = itemIds;
    }
    setTotal(total) {
        this.total = total;
    }
    // 
    getPayment() {
        return this.payment;
    }
    getAddress() {
        return this.address;
    }
    getEmail() {
        return this.email;
    }
    getPhone() {
        return this.phone;
    }
    // 
    validateStep1() {
        return !!(this.payment && this.address.trim());
    }
    validateStep2() {
        return !!(this.email.trim() && this.phone.trim());
    }
    /**
     *       .
     */
    getOrderData() {
        if (!this.validateStep1() || !this.validateStep2()) {
            return null;
        }
        return {
            payment: this.payment,
            email: this.email.trim(),
            phone: this.phone.trim(),
            address: this.address.trim(),
            total: this.total,
            items: this.items,
        };
    }
    /**
     *    .
     */
    async submitOrder() {
        const orderData = this.getOrderData();
        if (!orderData) {
            return Promise.reject('');
        }
        try {
            const response = await this.apiClient.post('/order', orderData);
            return response;
        }
        catch (error) {
            return Promise.reject(error);
        }
    }
    /**
     *      .
     */
    reset() {
        this.payment = null;
        this.address = '';
        this.email = '';
        this.phone = '';
        this.items = [];
        this.total = 0;
    }
}
