export class ContactInfoContentView {
    /**
     *  :
     * @param templateId - id 
     * @param onInputChanged -       .
     */
    constructor(templateId, onInputChanged) {
        this.submitButton = null;
        this.errorElement = null;
        this.onInputChanged = onInputChanged;
        const template = document.getElementById(templateId);
        if (!template) {
            throw new Error(`ContactInfoContentView:  `);
        }
        const clone = template.content.cloneNode(true);
        const element = clone.firstElementChild;
        if (!element) {
            throw new Error(`ContactInfoContentView:  `);
        }
        this.contentElement = element;
    }
    //        .
    render() {
        const emailInput = this.contentElement.querySelector('input[name="email"]');
        const phoneInput = this.contentElement.querySelector('input[name="phone"]');
        this.submitButton = this.contentElement.querySelector('button');
        this.errorElement = this.contentElement.querySelector('.form__errors');
        if (this.submitButton) {
            this.submitButton.disabled = true;
        }
        emailInput.addEventListener('input', () => {
            this.onInputChanged('email', emailInput.value);
        });
        phoneInput.addEventListener('input', () => {
            this.onInputChanged('phone', phoneInput.value);
        });
        const form = this.contentElement.querySelector('form');
        if (form) {
            form.addEventListener('submit', (e) => {
                e.preventDefault();
            });
        }
    }
    /**
     *     "".
     * @param callback - ,     .
     */
    bindSubmitButton(callback) {
        if (this.submitButton) {
            this.submitButton.addEventListener('click', callback);
        }
    }
    /**
     *        .
     * @param callback -  (field, value) => void.
     */
    setOnInputChanged(callback) {
        this.onInputChanged = callback;
    }
    /**
     *      .
     * @param errorMessage -  .
     */
    setError(errorMessage) {
        if (this.errorElement) {
            this.errorElement.textContent = errorMessage;
        }
    }
    /**
     *    "".
     * @param enabled - true,     .
     */
    setButtonState(enabled) {
        if (this.submitButton) {
            this.submitButton.disabled = !enabled;
        }
    }
    /**
     *  :       ,     .
     */
    reset() {
        const emailInput = this.contentElement.querySelector('input[name="email"]');
        const phoneInput = this.contentElement.querySelector('input[name="phone"]');
        if (emailInput) {
            emailInput.value = '';
        }
        if (phoneInput) {
            phoneInput.value = '';
        }
        this.setError('');
        this.setButtonState(false);
    }
    /**
     *   DOM- .
     */
    getContent() {
        return this.contentElement;
    }
}
