export class OrderSuccessContentView {
    /**
     *   id  "success"    .
     * @param templateId - id 
     * @param closeHandler - ,      
     */
    constructor(templateId, closeHandler) {
        const template = document.getElementById(templateId);
        if (!template) {
            throw new Error(`OrderSuccessContentView:  `);
        }
        const clone = template.content.cloneNode(true);
        const element = clone.firstElementChild;
        if (!element) {
            throw new Error(`OrderSuccessContentView:  `);
        }
        this.contentElement = element;
        this.closeHandler = closeHandler;
    }
    /**
     *     ,   .
     * @param finalTotal -  ,  .
     */
    render(finalTotal) {
        //  ,    .
        const descriptionElement = this.contentElement.querySelector('.order-success__description');
        if (descriptionElement) {
            descriptionElement.textContent = ` ${finalTotal} `;
        }
        //     "  !"
        const closeBtn = this.contentElement.querySelector('.order-success__close');
        if (closeBtn) {
            closeBtn.addEventListener('click', this.closeHandler);
        }
    }
    /**
     *   DOM-   .
     */
    getContent() {
        return this.contentElement;
    }
}
