import { HEADER_SELECTOR, BASKET_COUNTER_SELECTOR, } from '../../utils/constants';
export class HeaderView {
    constructor(selector = HEADER_SELECTOR) {
        const root = document.querySelector(selector);
        if (!root) {
            throw new Error(`HeaderView:    "${selector}"`);
        }
        this.rootElement = root;
        const counter = this.rootElement.querySelector(BASKET_COUNTER_SELECTOR);
        if (!counter) {
            throw new Error(`HeaderView:    "${BASKET_COUNTER_SELECTOR}"`);
        }
        this.basketCounterElement = counter;
    }
    renderCartCount(count) {
        this.basketCounterElement.textContent = count.toString();
    }
}
